import { Grid } from "@mui/material";
import { computerScreenIcon, intranetIcon } from "../../../imgs/image";

export const FunctionalityContainer = () => {
  return (
    <div className="bg-primary-subtle">
      <div className="container text-center pb-5">
        <h1 className="fs-2 fw-bold pt-5 pb-5"></h1>
        <Grid container justifyContent={"center"} padding={2}>
          <Grid
            item
            xs={12}
            md={4}
            className="d-flex mx-5 justify-content-center"
          >
            <div className="container d-flex">
              <img
                src={computerScreenIcon}
                alt="computer screen"
                className="img-fluid"
              ></img>
            </div>
          </Grid>
          <Grid
            item
            md={4}
            className="container mx-5 d-flex flex-column justify-content-center"
          >
            <h3 className="fs-4 fw-bold "></h3>
            <p className="fs-6 fw-bold">
              
            </p>
            <h3 className="fs-4 fw-bold "></h3>
            <p className="fs-6 fw-bold">
              ,IT
              
            </p>
            <h3 className="fs-4 fw-bold "></h3>
            <p className="fs-6 fw-bold">
              
              
              []
            </p>
          </Grid>
        </Grid>
        <Grid container padding={2} justifyContent={"center"}>
          <Grid
            item
            xs={12}
            md={4}
            className="container mx-5 d-flex flex-column justify-content-center"
          >
            <h3 className="fs-4 fw-bold "></h3>
            <p className="fs-6 fw-bold">
              
              
            </p>
            <h3 className="fs-4 fw-bold "></h3>
            <p className="fs-6 fw-bold">
              
              
            </p>
          </Grid>
          <Grid
            item
            xs={12}
            md={4}
            className="d-flex mx-5 justify-content-center"
          >
            <div className="container d-flex">
              <img
                src={intranetIcon}
                alt="computer screen"
                className="img-fluid"
              ></img>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};
